.card {
  width: $card-width;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background-color: #fff;
  padding: 32px 16px 16px;

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    color: $supplement-text-color;
    margin: 0 0 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &__product-code {
    margin-bottom: 16px;
  }

  &__price-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price {
    font-size: 12px;
    line-height: 18px;
  }

  &__sum {
    color: $supplement-text-color;
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
  }

  &__review-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__review-points {
    color: $supplement-text-color;
  }

  &__buy-button {
    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    height: 40px;
    border: 1px solid $main-color;
    border-radius: 5px;

    text-align: center;
    line-height: 16px;
    background-color: $main-color;
    text-transform: uppercase;
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
  }

  &__buy-button:hover {
    color: $main-color;
    background-color: #fff;
  }
}
